.about {
  &__title {
    padding: 30px 0;
    margin: 0 0 32px;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $colorWhite;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
      margin-bottom: 0;
    }
  }

  &__paragraph {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
    color: $colorWhite;
    margin: 0;
  }
  
}
